























import {Component, Prop, Ref, Vue} from "vue-property-decorator";


@Component({})
export default class RealtyCard2 extends Vue {
  @Ref('balloon')
  refBalloon!: HTMLElement
  @Prop({ required: true })
  imgPath!: string
  @Prop({ required: true })
  name!: string
  @Prop({ required: true })
  area!: number
  @Prop({ required: true })
  description!: string
  @Prop({ required: true })
  price!: number
  @Prop({ required: true })
  id!: number
  @Prop({ required: true })
  discount!: number


  onClick (): void {
    this.$router.push({name: 'viewRealty', params: {category: 'category-name', id: this.id + ''}})
  }

}

